/**
 * Scope de recherche par dÃ©faut sur tout l'atelier.
 *
 * NOTE : exclusion cachÃ©e d'espaces via une pref : ItemCritFullWsp.excludeSpaces
 * Valeur : "/~hidden" ou "/~hidden;/~params"
 */
export class ItemCritFullWsp extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-fullwsp")
		.setLabel("Tout l'atelier")
		.setCritSgn("scope").setReplacePattern(/\bscope\b/) //.setGroup("scope") pas un critÃ¨re/filtre
		.setBuildAbstract(function (this: UiCritArea, xml: Element, ctx: OUiCritInit) {
			const air = xml.getAttribute("uiAir") === 'true';
			const foreign = xml.getAttribute("uiForeign") === 'true';
			return <span class="crit">{this.getLabel(ctx) + (air ? foreign ? ", items flottants et Ã©trangers inclus" : ", items flottants inclus" : foreign ? ", items Ã©trangers inclus" : "")}</span>;
		});

	protected _airItems: HTMLInputElement;
	protected _foreignItems: HTMLInputElement;

	get withAirItems(): boolean {return this._airItems ? this._airItems.checked : false}

	get withForeignItems(): boolean {return this._foreignItems ? this._foreignItems.checked : false}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const wsp = this.reg.env.wsp;
		const sr = this.shadowRoot;
		sr.appendChild(<div class="critHead">
			<span class="critTitle">Tout l'atelier</span>
			{this.insertAlternativesBtn(init)}
		</div>);
		let optsRoot = sr as Node;
		if (wsp.isAirItem && wsp.isExtItem) optsRoot = sr.appendChild(<div class="h"/>);
		if (wsp.isAirItem) this._airItems = optsRoot.appendChild(<label class="opt"><input type="checkbox" checked="" onclick={this.onChange}/>Items flottants inclus</label>).firstElementChild as HTMLInputElement;
		if (wsp.isExtItem) this._foreignItems = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>Items Ã©trangers inclus</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		if (this._airItems) this._airItems.checked = xml.getAttribute("uiAir") === 'true';
		if (this._foreignItems) this._foreignItems.checked = xml.getAttribute("uiForeign") === 'true';
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const crit = new SearchItems().excludeAirItems(!this.withAirItems).excludeExtItems(!this.withForeignItems).includeDrfErased(this.reg.env.wsp.wspDefProps.drfRefWsp != null);
		const fixExclude = this.reg.getPref<string>("ItemCritFullWsp.excludeSpaces"); //Valeur : "/~hidden" ou "/~hidden;/~params"

		if (fixExclude) crit.excludeSpaces(...fixExclude.split(';'));
		return crit;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit = this.buildCrit().toDom();
		crit.setAttribute("uiCrit", this.getUiCritXmlAtt());
		if (this.withAirItems) crit.setAttribute("uiAir", "true");
		if (this.withForeignItems) crit.setAttribute("uiForeign", "true");
		return crit;
	}
}

