/**
 *
 */
class UiCritLinksArea extends UiCritArea {

	constructor(public linkAxis: 'LinkAsc' | 'LinkDesc' | 'LinkChildren' | 'LinkParents', protected abstractStrPrefix?: string, public traitsCodeList: string|null = "linktraits:search:defaultList") {
		super("wsp-itemcrit-links");
		this.setReplacePattern(/\bscope\b/);
		this.setCritSgn("scope");
		this.setGroup("scope");
	}

	buildAbstract(xml: Element, ctx: OUiCritInit, options?: any): HTMLElement | undefined {
		if (this.abstractStrPrefix) {
			const path = xml.getAttribute('path') || "";
			let result = super.buildAbstract(xml, ctx, options);
			if (path) {
				let srcPointer: SrcPointerInline = <SrcPointerInline Ã®={{
					reg: ctx.reg,
				} as OItemPointerInit}/> as SrcPointerInline;
				Promise.resolve(srcPointer.setSrcRef(path)).then(() => {
					result.innerText = this.abstractStrPrefix;
					result.appendChild(<span class="critVal"/>).appendChild(srcPointer);
					const linkTraitFilter = xml.getAttribute('linkTraitFilter') || "";
					if(linkTraitFilter){
						const traits = ctx.reg.getListAsMap(this.traitsCodeList) as Dict<string> || {};
						result.appendChild(<span>(filtre '{traits[linkTraitFilter] || linkTraitFilter}')</span>)
					}
				});
			}
			return result;
		} else
			return super.buildAbstract(xml, ctx, options);
	}

	setLinkAxis(linkAxis: 'LinkAsc' | 'LinkDesc' | 'LinkChildren' | 'LinkParents'): this {
		this.linkAxis = linkAxis;
		return this;
	}
}

/**
 * Scope de recherche parcourant les liens Ã  partir d'un item.
 */
export class ItemCritLinks extends UiCritBase {

	static AREA_DESC = new UiCritLinksArea("LinkDesc", "Dans le rÃ©seau descendant de l'item").setLabel("RÃ©seau descendant d'un item");

	static AREA_ASC = new UiCritLinksArea("LinkAsc", "Dans le rÃ©seau ascendant de l'item").setLabel("RÃ©seau ascendant d'un item");

	static AREA_CHILDREN = new UiCritLinksArea("LinkChildren", "Dans les items liÃ©s de l'item").setLabel("Items liÃ©s par un item");

	static AREA_PARENTS = new UiCritLinksArea("LinkParents", "Dans les items liants l'item").setLabel("Items liant un item")
		.setBuildWedSearch((xml: Element, criterions: Dict<IUiCritArea>): JItemWedSearchBarParams => {
			return {
				searchId: "item",
				findRef: xml.getAttribute('path')
			}
		});

	critArea: UiCritLinksArea;

	get linkAxis(): 'LinkAsc' | 'LinkDesc' | 'LinkChildren' | 'LinkParents' {return this.critArea.linkAxis}

	get traitsCodeList():string|null{return this.critArea.traitsCodeList}

	itemPtr: SrcPointerInline;

	trFilter : HTMLSelectElement|null;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		const axis = this.linkAxis;
		this.itemPtr = <SrcPointerInline Ã®={{
			reg: this.reg,
			defaultAction: SrcPointerFastSelect.SINGLETON
		} as OItemPointerInit}/> as SrcPointerInline;

		sr.appendChild(<div class="critHead">
			<span class="critTitle">{axis === 'LinkDesc' ? "Descendants de" : axis === 'LinkAsc' ? "Ascendants de" : axis === 'LinkChildren' ? "Items liÃ©s par" : "Items liants"}</span>
			{this.itemPtr}
			{this.insertAlternativesBtn(init)}
		</div>);

		if(this.traitsCodeList){
			const traits = this.reg.getListAsMap(this.traitsCodeList) as Dict<string>;
			if(traits){
				this.trFilter = <select onchange={this.onChange}>
					<option value="" selected>Aucun filtre</option>
				</select> as HTMLSelectElement;
				for (const tr in traits)
					this.trFilter.appendChild(<option value={tr}>{traits[tr]}</option>);
				sr.appendChild(<div class="h">
					{this.trFilter}
				</div>)
			}
		}

		this.itemPtr.onSrcRefChange.add(this.onChange);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const path = xml.getAttribute('path');
		if (path) {
			await this.itemPtr.setSrcRef(path);
			if (!this.itemPtr.shortDesc || ITEM.getSrcUriType(this.itemPtr.shortDesc.srcUri) != "item")
				this.itemPtr.setSrcRef(null);
		}
		const linkTraitFilter = xml.getAttribute('linkTraitFilter');
		if(linkTraitFilter && this.trFilter)
			this.trFilter.value =linkTraitFilter

		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		const path = xml ? xml.getAttribute('path') : null;
		if (path) {
			await this.itemPtr.setSrcRef(path);
			if (!this.itemPtr.shortDesc || ITEM.getSrcUriType(this.itemPtr.shortDesc.srcUri) != "item") {
				this.itemPtr.setSrcRef(null);
				await this.itemPtr.openFastSelect();
			}
		} else if (!this.itemPtr.srcRef) {
			await this.itemPtr.openFastSelect();
		}

		const linkTraitFilter = xml ? xml.getAttribute('linkTraitFilter') : null;
		if(linkTraitFilter && this.trFilter)
			this.trFilter.value =linkTraitFilter

		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		return new SearchItemLinks(this.linkAxis).setPath(this.itemPtr.srcRef).setLinkTraitFilter(this.trFilter?.value);
	}

	protected getUiCritXmlAtt(): string {
		return this.linkAxis;
	}

}

