/**
 * Scope de recherche Ã  partir d'un espace.
 */
export class ItemCritInSpace extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-inspace").setLabel("Dans un espace").setCritSgn("scope").setGroup("scope").setReplacePattern(/\bscope\b/).setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
		const path = xml.getAttribute('uiPath') || "";
		return path ? <span class="crit">Dans l'espace '<span class="critVal">{path}</span>'</span> : <span class="crit">Dans un espace</span>;
	});
	//this.setCritSgn("scope");
	//this.setGroup("scope");
	_spaceUri: srcUri;

	get spaceUri(): srcUri {return this._spaceUri};

	set spaceUri(uri: srcUri) {
		this._spaceUri = ITEM.extractSpaceUri(uri);
		this._spaceBtn.label = this.spaceUri || " ";
	}

	protected _spaceBtn: Button;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		sr.appendChild(<div class="critHead">
			<span class="critTitle">Dans l'espace</span>
			{this.insertAlternativesBtn(init)}
		</div>);
		this._spaceBtn = sr.appendChild(<Button id="space" icon={this.reg.env.wsp.wspMetaUi.getSpaceIcon()} onclick={this.onChooseSpace} title="SÃ©lectionner un espace..."/>) as Button;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const path = xml.getAttribute('uiPath');
		if (path != null && !SRC.isSrcId(path)) this.spaceUri = path;
		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		const path = xml ? xml.getAttribute('uiPath') : null;
		if (path != null && !SRC.isSrcId(path)) {
			this.spaceUri = path;
		} else if (!this.spaceUri) {
			await this.onChooseSpace.call(this._spaceBtn);
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this.spaceUri == null) return undefined;
		return new SearchItems().inSpace(this.spaceUri);
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit && this.spaceUri) crit.setAttribute("uiPath", this.spaceUri);
		return crit;
	}

	async onChooseSpace(this: Button, ev?: Event) {
		const me = DOMSH.findHost(this) as ItemCritInSpace;
		const {SpaceSelector} = await import("back/wsp/dialogs/spaceSelector.js");
		const ct = new SpaceSelector().initialize({
			reg: me.reg,
			startSel: me.spaceUri,
			selOnDblClick: true
		});
		const space = await POPUP.showMenuFromEvent<JSrcFields | null>(ct, ev || me, me).onNextClose();
		if (space) {
			me.spaceUri = space.srcUri;
			me._spaceBtn.label = me.spaceUri;
			me.dispatchChangeEvent();
		}
	}
}

