/**
 * Recherche full text
 */
export class ItemCritFullText extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-fulltext").setLabel("Texte")
		.setCritSgn("string").setReplacePattern(/\bstring\b/).setGroup("props").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const txt = xml.getAttribute("textSearch") || "";
			const opts = xml.getAttribute("options") || "";
			return <span class="crit">Texte contient '<span class="critVal">{txt}</span>'{opts.indexOf(';w;') >= 0 ? ', mot entier' : undefined}{opts.indexOf(';c;') >= 0 ? ', casse respectÃ©e' : undefined}</span>;
		}).setBuildWedSearch((xml: Element, criterions: Dict<IUiCritArea>): JTextWedSearchBarParams => {
			const opts = xml.getAttribute("options") || "";
			return {
				searchId: "text",
				text: xml.getAttribute("textSearch") || "",
				wholeWord: opts.indexOf(';w;') >= 0,
				matchCase: opts.indexOf(';c;') >= 0
			}
		});

	protected _text: HTMLInputElement;
	protected _ignoreCase: HTMLInputElement;
	protected _wholeWord: HTMLInputElement;

	protected _initialize(init: OUiCritInit & OUiCritWithInputInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._text = sr.appendChild(<div class="critHead">
			<span class="critTitle">Texte</span>
			<input type="search" oninput={this.onChange} placeholder={init.placeholder}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("input");
		const optsRoot = sr.appendChild(<div class="h"/>);
		this._ignoreCase = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>Respect de la casse</label>).firstElementChild as HTMLInputElement;
		this._wholeWord = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>Mot entier</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._text.value = xml.getAttribute('textSearch') || "";
		const opts = xml.getAttribute('options') || "";
		this._ignoreCase.checked = opts.indexOf(';c;') >= 0;
		this._wholeWord.checked = opts.indexOf(';w;') >= 0;
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._text.value;
		return v ? new SearchFullText(v, this._ignoreCase.checked, this._wholeWord.checked) : undefined;
	}

}

