/**
 * Code de l'item
 */
export class ItemCritSrcCode extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-srccode").setLabel("Code de l'item")
		.setCritSgn("string").setReplacePattern(/\bstring\b/).setGroup("props").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const searchTxt = <span class="critVal">{xml.getAttribute("uiTextSearch") || ""}</span>;
			return <span class="crit">Code de l'item contient '{searchTxt}'</span>;
		});

	protected _textElt: HTMLInputElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._textElt = sr.appendChild(<div class="critHead">
			<span class="critTitle">Code de l'item</span>
			<input type="search" oninput={this.onChange}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("input");
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		this._textElt.value = xml.getAttribute('uiTextSearch') || "";
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._textElt.value;
		if (v) {
			const regexp = new RegExp(".*" + v.replace(/\W/g, LANG.escapeChar4Regexp) + "[^\/]*", "i");
			return new SearchRegexpUri(regexp);
		} else
			return undefined;
	}

	/**
	 * CritÃ¨res UI :
	 *  - uiTextSearch
	 */
	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit) crit.setAttribute("uiTextSearch", this._textElt.value);
		return crit;
	}

}

customElements.define("wsp-itemcrit-srccode", ItemCritSrcCode);

