/**
 * Recherche dans le titre
 */
export class ItemCritTitleRegExp extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-title").setLabel("Titre")
		.setCritSgn("string").setReplacePattern(/\bstring\b/).setGroup("props").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const searchTxt = <span class="critVal">{xml.getAttribute("uiTextSearch") || xml.getAttribute("regexp")}</span>;
			return <span class="crit">Titre contient '{searchTxt}'</span>;
		});

	protected _textElt: HTMLInputElement;
	protected _regExpElt: HTMLInputElement;

	get isRegExp(): boolean {return this._regExpElt ? this._regExpElt.checked : false}

	protected _initialize(init: OUiCritInit & OUiCritWithInputInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this._textElt = sr.appendChild(<div class="critHead">
			<span class="critTitle">Titre</span>
			<input type="search" oninput={this.onChange} placeholder={init.placeholder}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("input");
		const optsRoot = sr.appendChild(<div class="h"/>);
		this._regExpElt = optsRoot.appendChild(<label class="opt"><input type="checkbox" onclick={this.onChange}/>Expression rÃ©guliÃ¨re</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		if (xml.hasAttribute('uiTextSearch')) {
			// texte
			this._textElt.value = xml.getAttribute('uiTextSearch') || "";
			this._regExpElt.checked = false;
		} else {
			// regExp
			this._textElt.value = xml.getAttribute('regexp') || "";
			this._regExpElt.checked = true;
		}
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._textElt.value;
		if (v && this.isRegExp) {
			return new SearchItemTitleRegexp(new RegExp(v));
		} else if (v) {
			const regexp = new RegExp(".*" + v.replace(/\W/g, LANG.escapeChar4Regexp) + ".*", "i");
			return new SearchItemTitleRegexp(regexp);
		} else
			return undefined;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit && !this.isRegExp) crit.setAttribute("uiTextSearch", this._textElt.value);
		return crit;
	}

}

