/**
 * Statut de l'item
 *    <wsp-itemcrit-status withWarning="false"/>
 */
export class ItemCritStatus extends UiCritBase {

	static STATUS: { [index: string]: any } = {
		//"min-max" : [longLabel, shortLabel]
		"3-3": ["Items en erreur", "en erreur"],
		"2-2": ["Items en alerte", "en alerte"],
		"1-1": ["Items valides", "valides"],
		"2-3": ["Items en erreur ou en alerte", "en erreur ou en alerte"],
		"1-2": ["Items valides ou en alerte", "valides ou en alerte"],
	};

	static AREA = new UiCritArea("wsp-itemcrit-status").setLabel("Statut de l'item")
		.setCritSgn("noProps").setReplacePattern(/\bnoProps\b/).setGroup("item").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const itemStatusMin = xml.getAttribute("itemStatusMin") || "";
			const itemStatusMax = xml.getAttribute("itemStatusMax") || "";
			return <span class="crit">Statut <span class="critVal">{ItemCritStatus.STATUS[itemStatusMin + "-" + itemStatusMax][1]}</span></span>;
		});

	protected _statusElt: HTMLSelectElement;

	protected _withWarning: Boolean = false;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);

		this._withWarning = this.hasAttribute("withWarning") ? this.getAttribute("withWarning") == "true" : this.reg.getPref("itemCritStatus.withWarning", false);

		const sr = this.shadowRoot;
		this._statusElt = sr.appendChild(<div class="critHead">
			<span class="critTitle">Statut</span>
			<select onchange={this.onChange}/>
			{this.insertAlternativesBtn(init)}
		</div>).querySelector("select");
		this._statusElt.appendChild(<option value="3-3" selected>{ItemCritStatus.STATUS["3-3"][0]}</option>);
		if (this._withWarning) this._statusElt.appendChild(<option value="2-2">{ItemCritStatus.STATUS["2-2"][0]}</option>);
		this._statusElt.appendChild(<option value="1-1">{ItemCritStatus.STATUS["1-1"][0]}</option>);
		if (this._withWarning) this._statusElt.appendChild(<option value="2-3">{ItemCritStatus.STATUS["2-3"][0]}</option>);
		if (this._withWarning) this._statusElt.appendChild(<option value="1-2">{ItemCritStatus.STATUS["1-2"][0]}</option>);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const itemStatusMin: number = Number.parseInt(xml.getAttribute("itemStatusMin")) || 3;
		const itemStatusMax: number = Number.parseInt(xml.getAttribute("itemStatusMax")) || 3;
		this._statusElt.value = itemStatusMin + "-" + itemStatusMax
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const v = this._statusElt.value.split("-");
		return v ? new SearchStatus(Number.parseInt(v[0]), Number.parseInt(v[1])) : undefined;
	}
}

