/**
 * Date de modification
 *    <wsp-itemcrit-lastmodif/>
 */
export class ItemCritLastModif extends UiCritBase {

	static OPERATORS: { [index: string]: string } = {
		//"op" : [shortLabel]
		"lt": "avant le",
		"gt=": "Ã  partir du",
		"lt=.lt": "le"
	};

	static AREA = new UiCritArea("wsp-itemcrit-lastmodif").setLabel("Date de modification").setDescription("Date de derniÃ¨re modification")
		.setCritSgn("dateProps").setReplacePattern(/\bdateProps\b/).setGroup("item").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const options: Intl.DateTimeFormatOptions = {year: 'numeric', month: 'numeric', day: 'numeric'};
			const op = xml.getAttribute("op") || "";
			const date = xml.getAttribute("value") || "";
			const dateObj: Date = new Date(Number.parseInt(date.split(",")[0]))
			const operatorTxt = ItemCritLastModif.OPERATORS[op];
			const dateTxt = <span class="critVal">{dateObj.toLocaleDateString("fr", options)}</span>;
			return <span class="crit">ModifiÃ©s {operatorTxt} {dateTxt}</span>;
		});

	protected _opElt: HTMLSelectElement;

	protected _dateElt: HTMLInputElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		const critHead = sr.appendChild(<div class="critHead">
			<span class="critTitle">Date de modification</span>
			<select onchange={this.onChange}/>
			<input type="date" onchange={this.onChange}/>
			{this.insertAlternativesBtn(init)}
		</div>);

		this._opElt = critHead.querySelector("select");
		this._opElt.appendChild(<option value="lt" selected>{ItemCritLastModif.OPERATORS["lt"]}</option>);
		this._opElt.appendChild(<option value="gt=" selected>{ItemCritLastModif.OPERATORS["gt="]}</option>);
		this._opElt.appendChild(<option value="lt=.lt" selected>{ItemCritLastModif.OPERATORS["lt=.lt"]}</option>);

		this._dateElt = critHead.querySelector("input");
		this._dateElt.valueAsDate = new Date();
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const operator = xml.getAttribute("op");
		const value = xml.getAttribute("value");
		this._opElt.value = operator;
		this._dateElt.valueAsDate = new Date(Number.parseInt(value.split(",")[0]));
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this._dateElt.value) {
			if (this._opElt.value == "lt=.lt") {
				const vStart = this._dateElt.valueAsDate;
				const vEnd = new Date(vStart.getFullYear(), vStart.getMonth(), vStart.getDate() + 1);
				return new SearchLastModifIn(this._opElt.value as EOperatorsIn, vStart, vEnd);
			} else
				return new SearchLastModif(this._opElt.value as EOperatorsSimple, this._dateElt.valueAsDate);
		}
		return undefined;
	}
}

