/**
 * Dernier utilisateur Ã  avoir modifiÃ© l'item
 *    <wsp-itemcrit-lastuser/>
 */
export class ItemCritLastUser extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-lastuser").setLabel("DerniÃ¨re personne Ã  avoir modifiÃ© l'item")
		.setCritSgn("userProps").setReplacePattern(/\buserProps\b/).setGroup("item").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const users = xml.getAttribute('users');
			if (users) {
				let accounts = users.split(" ");
				const usersListVal = <span class="critVal">{accounts.join(", ")}</span>;
				const resulElt = <span class="crit">ModifiÃ© par {usersListVal}</span>;
				ctx.reg.env.universe.useUsers.getUserSet(accounts).then(users => {
					if (users) {
						accounts = [];
						const names: string[] = [];
						users.forEach(user => {
							accounts.push(USER.getPrimaryName(user));
							names.push(USER.getLongName(user));
						});
						usersListVal.textContent = accounts.join(", ");
						usersListVal.setAttribute("title", names.join(", "));
					}
				});
				return resulElt;
			}
		}).setVisible((ctx) => {
			return !ctx.reg.env.universe.auth.config.noAuthentication;
		});

	_selectedUsers: JUser[];

	get selectedUsers(): JUser[] {return this._selectedUsers};

	set selectedUsers(users: JUser[]) {
		this._selectedUsers = users;
		this.refreshUi();
	}

	protected _userBtn: Button;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		const critHead = sr.appendChild(<div class="critHead">
			<span class="critTitle">DerniÃ¨re personne Ã  avoir modifiÃ© l'item</span>
			{this.insertAlternativesBtn(init)}
		</div>);
		this._userBtn = sr.appendChild(<Button id="users" onclick={this.onChooseUser} title="SÃ©lectionner un utilisateur..."/>) as Button;
		this.refreshUi();
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const users = xml.getAttribute('users');
		if (users)
			this.selectedUsers = await this.reg.env.universe.useUsers.getUserSet(users.split(" "));
		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		await super.initFromInsAction(xml);
		if (!this.selectedUsers || this.selectedUsers.length == 0)
			await this.onChooseUser.call(this._userBtn);
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (this.selectedUsers == null) return undefined;
		return new SearchLastUser(this.selectedUsers);
	}

	async onChooseUser(this: Button, ev?: Event) {
		const me = DOMSH.findHost(this) as ItemCritLastUser;
		const {UserSelector} = await import("back/core/dialogs/userSelector.js");
		const userSelector = new UserSelector().initialize({
			reg: me.reg,
			startSel: me.selectedUsers,
			userGrid: {
				usersSrv: me.reg.env.universe.useUsers,
				grid: {
					selType: "multi"
				},
				filterTypeInputVisibility: true,
				filterType: EUserType.user,
				preserveSelectionOnFilter: true,
			},
			selectAndCloseOnDblClick: true
		});
		const user = await POPUP.showMenuFromEvent<JUser[] | null>(userSelector, ev || me, me, null, { initWidth: '20em'}).onNextClose();
		if (user) {
			me.selectedUsers = user;
			me.dispatchChangeEvent();
		}
	}

	private refreshUi(): void {
		while (this._userBtn.lastChild) this._userBtn.removeChild(this._userBtn.lastChild);
		if (this.selectedUsers && this.selectedUsers.length > 0) {
			this.selectedUsers.forEach((user) => {
				this._userBtn.appendChild(<div class="userEntry">
					<img src={USER.getIconUrl(user)}/>
					{USER.getPrimaryName(user)}
				</div>);
			});
		} else
			this._userBtn.appendChild(<div class="noEntry">Veuillez sÃ©lectionner des utilisateurs...</div>);
	}
}

