/**
 * Type de l'item (class)
 *    <wsp-itemcrit-type/>
 *    PossibilitÃ© de filter la liste des itemTypes proposÃ©s via
 *    	- la propriÃ©tÃ© 'itemTypeReducer'
 *    	- ou, Ã  dÃ©faut, le svc du registre `itemCritType.itemTypeReducer`
 */
export class ItemCritType extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-type").setLabel("Type de l'item")
		.setGroup("item").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			const itModels = xml ? xml.getAttribute('uiItModels') : null;
			const itLabels: string[] = [];
			if (itModels != null) {
				const itTypesArray = itModels.split("|");
				itTypesArray.forEach(itModel => {
					itLabels.push(ctx.reg.env.wsp.wspMetaUi.getItemType(itModel).getTitle());
				});
			}
			if (itLabels.length == 1) {
				const itLabel = <span class="critVal">{itLabels[0]}</span>;
				return <span class="crit">Type '{itLabel}'</span>;
			} else {
				const itCount = <span class="critVal">{itLabels.length}</span>;
				return <span class="crit" title={itLabels.join(", ")}>{itCount} types</span>;
			}
		});

	/** Reducer des itemType proposÃ©s*/
	public itemTypeReducer : (acc: ItemType[], cur: ItemType, idx?: number, src?: ItemType[]) => ItemType[] = null;

	/** Contient la sÃ©lection courante */
	protected _selectedItModels: string[];

	protected _itemTypesPopup: HTMLElement;

	/** UtilisÃ© uniquement pour l'affichage  */
	protected _itemTypesTree: ItemTypesTree;

	protected _itemsBtn: Button;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		sr.appendChild(<div class="critHead">
			<span class="critTitle">Type(s) :</span>
			{this.insertAlternativesBtn(init)}
		</div>);

		// Popup de sÃ©lection
		//let template = sr.appendChild(<template/>);
		this._itemTypesPopup = <div id="root"/>;
		this._itemTypesPopup.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("wsp-itemcrit-type/selector", this._itemTypesPopup.shadowRoot);

		const itemTypeReducer = this.itemTypeReducer || this.reg.getSvc("itemCritType.itemTypeReducer");

		this._itemTypesTree = this._itemTypesPopup.shadowRoot.appendChild(new ItemTypesTree().initialize({
			reg: this.reg,
			textFilter: "",
			itemTypeReducer,
			forbidHideSelectedRow: true,
			grid: {
				selType: "multi"
			}
		}));
		this._itemTypesTree.setAttribute("style", "max-height: 20em;");
		const footer = this._itemTypesPopup.shadowRoot.appendChild(<div id="footer">
			<div id="msg"/>
		</div>);
		footer.appendChild(<Button id="select" label="SÃ©lectionner" ui-context="dialog" onclick={this.chooseItems_onSelectBtn.bind(this)}/>);
		footer.appendChild(<Button id="cancel" label="Annuler" ui-context="dialog" onclick={this.chooseItems_onCancelBtn.bind(this)}/>);

		// Btn
		this._itemsBtn = sr.appendChild(<Button id="itemsTypes" label=" " onclick={this.onChooseItems} title="SÃ©lectionner des types d'item..."/>) as Button;

		this.redraw();
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		const itTypes = xml ? xml.getAttribute('uiItModels') : null;
		this._selectedItModels = itTypes?.split("|");
		this.redraw();
		return this;
	}

	async initFromInsAction(xml?: Element): Promise<this> {
		await super.initFromInsAction(xml);
		// Affichage auto du popup sur la crÃ©ation
		if (!this._selectedItModels || this._selectedItModels.length == 0)
			await this.onChooseItems.call(this._itemsBtn);
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		if (!this._selectedItModels || this._selectedItModels.length == 0) return undefined;
		// @see morphologie signature Wui_Modeling\doc\class\signature.md
		return new SearchItemSgnRegexp(this._selectedItModels?.map(entry => '.*@' + LANG.escape4Regexp(entry) + "\\b.*"));
	}

	/**
	 *  CritÃ¨res UI :
	 *    uiItModels : liste des itModels sÃ©lectionnÃ©s, sÃ©papÃ©s par un |
	 */
	buildUiXmlCrit(): Element | undefined {
		const itModels: string[] = [];
		const crit: Element = super.buildUiXmlCrit();
		if (crit && this._selectedItModels) crit.setAttribute("uiItModels", this._selectedItModels.join("|"));
		return crit;
	}

	/**
	 * Redraw UI
	 *    MÃ©morisation des sÃ©lctions dans "_itemTypesTree"
	 */
	private redraw(): void {
		while (this._itemsBtn.firstChild) this._itemsBtn.removeChild(this._itemsBtn.firstChild);
		if (this._selectedItModels && this._selectedItModels.length > 0) {
			this._selectedItModels.forEach(itModel => {
				const itemType = this.reg.env.wsp.wspMetaUi.getItemType(itModel);
				if (itemType) {
					this._itemsBtn.appendChild(<div class="modelEntry">
						<img src={itemType.getIcon()}/>
						{itemType.getTitle()}
					</div>);
				}
			});
		} else
			this._itemsBtn.appendChild(<div class="noEntry">Veuillez sÃ©lectionner des types d'item...</div>);
	}

	async onChooseItems(this: Button, ev?: Event) {
		const me = DOMSH.findHost(this) as ItemCritType;
		me._itemTypesTree.clearSearch();
		me._itemTypesTree.selectByItModels(me._selectedItModels);
		await POPUP.showMenuFromEvent(me._itemTypesPopup, ev || me, me, {skinOver: "wsp-itemcrit-type-popup"}).onNextClose();
	}

	chooseItems_onCancelBtn(this: ItemCritType) {
		POPUP.findPopupableParent(this._itemTypesPopup).close();
	}

	chooseItems_onSelectBtn(this: ItemCritType) {
		this._selectedItModels = [];
		this._itemTypesTree.getSelectedItemsTypes().forEach(itemType => {
			this._selectedItModels.push(itemType.getModel());
		});
		this.dispatchChangeEvent();
		this.redraw();
		POPUP.findPopupableParent(this._itemTypesPopup).close();
	}

}

