/**
 * Item commentÃ©
 *    <wsp-itemcrit-comment/>
 */
export class ItemCritComment extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-comment").setLabel("Item commentÃ©")
		.setGroup("props").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {
			let msg = "";
			if (xml.hasAttribute("uiIncludeClosed") && (xml.hasAttribute("uiIncludeOpen")))
				msg = "Commentaires ouverts ou clos";
			else if (xml.hasAttribute("uiIncludeClosed"))
				msg = "Commentaires clos";
			else
				msg = "Commentaires ouverts";
			return <span class="crit" title={msg}>Item commentÃ©</span>;
		}).setBuildWedSearch((xml: Element, criterions: Dict<IUiCritArea>): JCommentWedSearchBarParams => {
			return {
				searchId: "comment",
				opened: xml.hasAttribute("uiIncludeOpen"),
				closed: xml.hasAttribute("uiIncludeClosed")
			}
		});

	protected opened: HTMLInputElement;

	protected closed: HTMLInputElement;

	get withOpenComments(): boolean {return this.opened ? this.opened.checked : false}

	get withClosedComments(): boolean {return this.closed ? this.closed.checked : false}

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;

		const critHead = sr.appendChild(<div class="critHead">
			<span class="critTitle">Item commentÃ©</span>
			{this.insertAlternativesBtn(init)}
		</div>);

		this.opened = critHead.appendChild(<label class="opt"><input type="checkbox" checked="" onclick={this.onClick}/>ouverts</label>).firstElementChild as HTMLInputElement;
		this.closed = critHead.appendChild(<label class="opt"><input type="checkbox" onclick={this.onClick}/>clos</label>).firstElementChild as HTMLInputElement;
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		if (xml.hasAttribute("uiIncludeOpen"))
			this.opened.checked = true;
		if (xml.hasAttribute("uiIncludeClosed"))
			this.closed.checked = true;
		return this;
	}

	protected onClick(this: HTMLInputElement): void {
		const me = DOMSH.findHost<ItemCritComment>(this);
		if (!me.opened.checked && !me.closed.checked) {
			//Les deux ne peuvent Ãªtre dÃ©cochÃ©s Ã  la fois, on rÃ©cative celui qui a Ã©tÃ© dÃ©cochÃ©.
			if (this === me.opened) {
				me.closed.checked = true;
			} else {
				me.opened.checked = true;
			}
		}
		me.onChange();
	}

	buildCrit(): ISearchCrit | undefined {
		return new SearchItemComment(this.withOpenComments, this.withClosedComments);
	}

	/**
	 * CritÃ¨res UI :
	 *  - uiIncludeOpen
	 *  - uiIncludeClosed
	 */
	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit && this.withOpenComments) crit.setAttribute("uiIncludeOpen", null);
		if (crit && this.withClosedComments) crit.setAttribute("uiIncludeClosed", null);
		return crit;
	}
}


customElements.define("wsp-itemcrit-comment", ItemCritComment);

REG.reg.registerSvc("wsp-itemcrit-comment", 1, ItemCritComment.AREA);
REG.reg.addSvcToList("wsp.crit.items", "wsp-itemcrit-comment", 1, "wsp-itemcrit-comment");


/**
 * Cycle de vie de l'item
 *    <wsp-itemcrit-lifecycle/>
 */
export class ItemCritLifeCycle extends UiCritBase {

	static AREA = new UiCritArea("wsp-itemcrit-lifecycle")
		.setLabel((ctx) => ctx.reg.env.wsp.wspMetaUi.getLcName())
		.setVisible((ctx) => (ctx.reg.env.wsp.hasFeature("liveCycle") && ctx.reg.env.wsp.wspMetaUi.getLcStates() !== null) ? true : false)
		.setCritSgn("lifecycle").setGroup("item").setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {

			// Cycles de vie
			const lcStates = xml.getAttribute("lcStates") || "";
			const lcStatesArr = lcStates.split(" ");
			const lcStatesLabels: string[] = [];
			lcStatesArr.forEach((stateCode) => {
				const state = ctx.reg.env.wsp.wspMetaUi.getLcState(stateCode != LC_STATE_DEFAULT_KEY ? stateCode : "");
				if (state && state.name)
					lcStatesLabels.push(state.name)
				else if (state && state.code == "")
					lcStatesLabels.push(LC_STATE_DEFAULT_NAME)
				else
					lcStatesLabels.push(stateCode);//LC state inconnu
			})
			let strCplts = [];
			if (xml.getAttribute("uiCurrentMode"))
				strCplts.push(<span>pÃ©riode donnÃ©e</span>)
			const lcBys = xml.getAttribute("lcBys");
			if (lcBys) {
				let accounts = lcBys.split(" ");
				const usersListVal = <span>affectÃ© par '{accounts.join(", ")}'</span>;
				strCplts.push(usersListVal)
				ctx.reg.env.universe.useUsers.getUserSet(accounts).then(users => {
					if (users) {
						accounts = [];
						const names: string[] = [];
						users.forEach(user => {
							accounts.push(USER.getPrimaryName(user));
							names.push(USER.getLongName(user));
						});
						usersListVal.textContent = "affectÃ© par '" + accounts.join(", ") + "'";//FIXME L10n
						usersListVal.setAttribute("title", names.join(", "));
					}
				});
			}
			const lcName = ctx.reg.env.wsp.wspMetaUi.getLcName();
			const label = lcStatesLabels.length > 1 ? `Ãtats du ${lcName} :` : `Ãtat du ${lcName} :`;
			const result: HTMLSpanElement = <span class="crit">{label} <span class="critVal">{lcStatesLabels.join(", ")}</span></span>;

			if (strCplts.length > 0) {
				result.appendChild(document.createTextNode(" ("));
				strCplts.forEach(((elt, pos) => {
					if (pos > 0 && pos < strCplts.length) result.appendChild(document.createTextNode(", "));
					result.appendChild(elt)
				}));
				result.appendChild(document.createTextNode(")"));
			}
			return result;
		});

	protected _lcElt: HTMLSelectElement;

	protected _dateElt: InputPeriod;

	protected _userElt: InputUserPanel<any>;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this.reg.installSkin("wsp-itemcrit-lifecycle", sr);

		this._lcElt = <select onchange={this.onChange} class="lcSelect" multiple="true"/> as HTMLSelectElement;
		this.reg.env.wsp.wspMetaUi.getLcStates().forEach((tr) => {
			let opt = this._lcElt.appendChild(<option value={tr.code || LC_STATE_DEFAULT_KEY}>{tr.name || LC_STATE_DEFAULT_NAME}</option>);
			opt.style.color = tr.color || "var(--color)";
			if (tr.iconUrl) opt.style.backgroundImage = `url("${tr.iconUrl}")`;
			if (tr.code == "") DOM.setAttrBool(opt, "selected", true);
		});

		this._dateElt = new InputPeriod().initialize();
		this._dateElt.title = "PÃ©riode pendant laquelle l'Ã©tat du cycle de vie a Ã©tÃ© affectÃ©";
		this._dateElt.addEventListener("change", this.onChange);
		this._dateElt.addEventListener("input", this.onChange);

		this._userElt = new InputUserPanel().initialize({
			name: "choiceUser",
			reg: this.reg,
			emptySelectionMsg: "SÃ©lectionner un utilisateur...",
			userCard: "multi",
			usersGridInit: {
				usersSrv: this.reg.env.universe.useUsers,
				grid: {selType: "mono"},
				filterType: EUserType.user,
				filterTypeInputVisibility: false,
			},
		} as OInputUserInit<any>) as InputUserPanel<any>;
		this._userElt.addEventListener("change", this.onChange);
		this._userElt.title = "Ãtat affectÃ© par l'un des utilisateurs";

		sr.appendChild(<div class="critHead">
			<span class="critTitle">{this.reg.env.wsp.wspMetaUi.getLcName()}</span>
			{this._lcElt}
			{this.insertAlternativesBtn(init)}
		</div>);
		sr.appendChild(<div class="h">
			<span>dÃ©fini</span>
			{this._dateElt}
			<span>par</span>
			{this._userElt}
		</div>);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		// LC
		this._lcElt.selectedIndex = -1;
		const lcStates = xml.getAttribute("lcStates") || "";
		const lcStatesArr = lcStates.split(" ");
		Array.from(this._lcElt.options).forEach(opt => {
			opt.selected = lcStatesArr.includes(opt.value)
		})
		// date
		this._dateElt.reset(xml.getAttribute("uiCurrentMode"), xml.hasAttribute("uiCurrentLess") ? Number.parseInt(xml.getAttribute("uiCurrentLess")) : null, xml.hasAttribute("uiCurrentMore") ? Number.parseInt(xml.getAttribute("uiCurrentMore")) : null);
		// by
		const lcBys = xml.getAttribute("lcBys");
		if (lcBys)
			this._userElt.value = lcBys.split(" ")

		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		let crit = this._dateElt.isSpecified() ? SearchLifeCycleStates.buildFromInputPeriod(this._dateElt) : new SearchLifeCycleStates();
		crit.lcStates = [];
		Array.from(this._lcElt.selectedOptions).forEach(opt => crit.lcStates.push(opt.value))
		crit.lcBys = this._userElt.value;
		return crit;
	}

	buildUiXmlCrit(): Element | undefined {
		const crit: Element = super.buildUiXmlCrit();
		if (crit && this._dateElt.isSpecified()) {
			crit.setAttribute("uiCurrentMode", this._dateElt.currentMode.getId());
			// Valeurs brutes (sans les regles d'exclusion/noemalisation qui sont fournies par le "currentMode")
			if (this._dateElt.date1.valueAsNumber) crit.setAttribute("uiCurrentLess", this._dateElt.date1.valueAsNumber.toString());
			if (this._dateElt.date2.valueAsNumber) crit.setAttribute("uiCurrentMore", this._dateElt.date2.valueAsNumber.toString());
		}
		return crit;
	}

}

