/**
 * ResponsabilitÃ©s de l'item
 *    <wsp-itemcrit-respsonsabilities/>
 */
export abstract class ItemCritRespsonsabilities extends UiCritBase {

	static AREA_item = ItemCritRespsonsabilities.makeArea('item', 'wsp-itemcrit-respsonsabilities');
	static AREA_task = ItemCritRespsonsabilities.makeArea('task', 'wsp-taskcrit-respsonsabilities');

	static makeArea(target: 'task' | 'item', tag: string): UiCritArea {
		return new UiCritArea(tag).setLabel("ResponsabilitÃ©s")
			.setVisible((ctx) => (ctx.reg.env.wsp.hasFeature("resp") && ctx.reg.env.wsp.wspMetaUi.hasResps) ? true : false)
			.setCritSgn("respsonsabilities").setGroup(target).setBuildAbstract((xml: Element, ctx: OUiCritInit): HTMLElement => {

				// Resps
				const modelResps = ctx.reg.env.wsp.wspMetaUi.listResps(target);
				const resps = xml.getAttribute("responsibilities") || "";
				const respsArr = resps.split(" ");
				const respsLabels: string[] = [];
				respsArr.forEach((resp) => {
					const currentResp = modelResps.find((entry) => entry.code === resp);
					respsLabels.push(currentResp ? currentResp.name : resp);
				})

				// users
				let usersListVal: HTMLElement;
				const users = xml.getAttribute("users");
				if (users) {
					const accounts = users.split(" ");
					const usersObj = <span/>;
					accounts.forEach((account, pos) => {
						if (pos > 0)
							usersObj.appendChild(<span> ou </span>);
						usersObj.appendChild(<UserRef class="inline list" Ã®={{reg: ctx.reg, nickOrAccount: account, withIcon: false} as OUserRefInit}/>)
					});
					usersListVal = respsLabels.length > 1 ?
						<span>attribuÃ©es Ã  '{usersObj}'</span> :
						<span>attribuÃ©e Ã  '{usersObj}'</span>;//FIXME L10n
					if (xml.getAttribute("expandUsers"))
						usersListVal.appendChild(<span> (Ã©tendu aux membres/groupes)</span>);
				}

				if (respsLabels.length > 1)
					return <span class="crit">ResponsabilitÃ©s '<span class="critVal">{respsLabels.join(" ou ")}</span>' {usersListVal}</span>;
				else
					return <span class="crit">ResponsabilitÃ© '<span class="critVal">{respsLabels}</span>' {usersListVal}</span>;
			});
	}

	protected target: 'task' | 'item';

	protected _respElt: HTMLSelectElement;

	protected _userElt: InputUserPanel<any>;

	protected _toGroupsElt: HTMLInputElement;

	protected _toMembersElt: HTMLInputElement;

	protected _initialize(init: OUiCritInit): void {
		super._initialize(init);
		const sr = this.shadowRoot;
		this.reg.installSkin("wsp-crit-respsonsabilities", sr);

		const withGroups = this.reg.env.universe.useUsers.hasAspect(EUserAspects.groupable);

		this._respElt = <select onchange={this.onChange} class="respSelect" multiple="true"/> as HTMLSelectElement;
		this.reg.env.wsp.wspMetaUi.listResps(this.target).forEach((resp) => {
			let opt = this._respElt.appendChild(<option value={resp.code}>{resp.name}</option>);
			if (resp.desc) opt.title = resp.desc;
		});

		this._userElt = new InputUserPanel().initialize({
			name: "choiceUser",
			reg: this.reg,
			emptySelectionMsg: withGroups ? "Utilisateurs/groupes..." : "Utilisateurs...",
			userCard: "multi",
			usersGridInit: {
				usersSrv: this.reg.env.universe.useUsers,
				filterTypeInputVisibility: true,
				filterType: null,
			},
		} as OInputUserInit<any>) as InputUserPanel<any>;
		this._userElt.addEventListener("change", this.onChange);
		this._userElt.title = withGroups ?
			"Au mois un utilisateur / groupe est concernÃ© par au moins une responsabilitÃ© sÃ©lectionnÃ©e." :
			"Au mois un utilisateur est concernÃ© par au moins une responsabilitÃ© sÃ©lectionnÃ©e.";

		let critGroups;
		if (withGroups) {
			this._toGroupsElt = <input type="checkbox" onclick={this.onChange}/> as HTMLInputElement;
			this._toMembersElt = <input type="checkbox" onclick={this.onChange}/> as HTMLInputElement;
			critGroups = <div class="h">
				<label class="opt"
							 title={this.target == "item" ? "Inclure les items associÃ©s aux groupes auxquels les utilisateurs sÃ©lectionnÃ©s appartiennent" : "Inclure les tÃ¢ches associÃ©es aux groupes auxquels les utilisateurs sÃ©lectionnÃ©s appartiennent"}>{this._toGroupsElt}+ groupes</label>
				<label class="opt" title={this.target == "item" ? "Inclure les items associÃ©s aux membres des groupes sÃ©lectionnÃ©s" : "Inclure les tÃ¢ches associÃ©es aux membres des groupes sÃ©lectionnÃ©s"}>{this._toMembersElt}+ membres</label>
			</div>;
		}

		const critHead = sr.appendChild(<div class="critHead">
			<span class="critTitle">ResponsabilitÃ©s</span>
			{this._respElt}
			{this.insertAlternativesBtn(init)}
		</div>);
		sr.appendChild(
			<div class="h">
				<span>attribuÃ©es Ã </span>
				<div class="v usersBox">
					{this._userElt}
					{critGroups}
				</div>
			</div>
		);
	}

	async initFromXmlCrit(xml: Element): Promise<this> {
		// - resp
		this._respElt.selectedIndex = -1;
		const resps = xml.getAttribute("responsibilities") || "";
		const respsArr = resps.split(" ");
		for (const opt of this._respElt.options) {
			opt.selected = respsArr.includes(opt.value)
		}
		// - users
		const users = xml.getAttribute("users");
		if (users)
			this._userElt.value = users.split(" ")
		// - options
		const expandUsers = xml.getAttribute("expandUsers");
		if (this._toGroupsElt)
			this._toGroupsElt.checked = expandUsers?.includes('toGroups');
		if (this._toMembersElt)
			this._toMembersElt.checked = expandUsers?.includes('toMembers');
		return this;
	}

	buildCrit(): ISearchCrit | undefined {
		const resps = [];
		for (const opt of this._respElt.selectedOptions) {
			resps.push(opt.value)
		}
		let crit = new SearchResponsibilities(resps, this._userElt.value, this._toMembersElt?.checked, this._toGroupsElt?.checked);
		return crit;
	}

}

